.premium-feature-container {
  position: relative;
  width: 100%;
}

.premium-feature-locked {
  background: rgba(255, 255, 255, 0.7);
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
  border-radius: 10px;
  padding: 20px;
  text-align: center;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 120px;
  border: 1px dashed #ccc;
}

.dark-mode .premium-feature-locked {
  background: rgba(45, 45, 45, 0.7);
  border-color: #444;
}

.premium-feature-locked:hover {
  background: rgba(255, 255, 255, 0.9);
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
}

.dark-mode .premium-feature-locked:hover {
  background: rgba(45, 45, 45, 0.9);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.15);
}

.lock-icon {
  font-size: 2rem;
  margin-bottom: 10px;
  color: #4a6bff;
}

.dark-mode .lock-icon {
  color: #7986cb;
}

.locked-message {
  font-size: 0.9rem;
  color: #666;
  margin: 0;
}

.dark-mode .locked-message {
  color: #bbb;
}

/* Стилі для модального вікна */
.upsell-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.upsell-modal {
  background: white;
  border-radius: 10px;
  padding: 30px;
  width: 90%;
  max-width: 400px;
  text-align: center;
  position: relative;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
}

.dark-mode .upsell-modal {
  background: #2d2d2d;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.4);
}

.close-upsell {
  position: absolute;
  top: 15px;
  right: 15px;
  background: none;
  border: none;
  font-size: 1rem;
  cursor: pointer;
  color: #666;
  transition: color 0.2s;
}

.close-upsell:hover {
  color: #333;
}

.dark-mode .close-upsell {
  color: #999;
}

.dark-mode .close-upsell:hover {
  color: #ccc;
}

.upsell-modal h3 {
  margin-top: 0;
  color: #333;
  font-size: 1.5rem;
  margin-bottom: 20px;
}

.dark-mode .upsell-modal h3 {
  color: #eee;
}

.upsell-icon {
  font-size: 3rem;
  margin: 10px 0 20px;
  color: #ffc107;
}

.feature-description {
  color: #666;
  margin-bottom: 25px;
  font-size: 0.95rem;
  line-height: 1.5;
}

.dark-mode .feature-description {
  color: #bbb;
}

.upsell-modal h4 {
  color: #666;
  margin: 0 0 10px 0;
  font-size: 0.9rem;
  font-weight: normal;
}

.dark-mode .upsell-modal h4 {
  color: #bbb;
}

.plan-required-badge {
  display: inline-block;
  padding: 6px 15px;
  background-color: #e8f5e9;
  color: #388e3c;
  border-radius: 20px;
  font-weight: 600;
  font-size: 0.9rem;
  margin-bottom: 20px;
}

.dark-mode .plan-required-badge {
  background-color: rgba(56, 142, 60, 0.2);
  color: #a5d6a7;
}

.upsell-actions {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.upgrade-btn {
  background-color: #4a6bff;
  color: white;
  border: none;
  padding: 12px 20px;
  border-radius: 6px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
  text-decoration: none;
  display: block;
  text-align: center;
}

.upgrade-btn:hover {
  background-color: #3a5aee;
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(74, 107, 255, 0.3);
}

.dark-mode .upgrade-btn {
  background-color: #7986cb;
}

.dark-mode .upgrade-btn:hover {
  background-color: #5c6bc0;
  box-shadow: 0 4px 8px rgba(121, 134, 203, 0.3);
}

.dismiss-btn {
  background: transparent;
  border: none;
  color: #666;
  padding: 8px;
  margin-top: 5px;
  cursor: pointer;
  font-size: 0.9rem;
  text-decoration: underline;
  transition: color 0.2s;
}

.dismiss-btn:hover {
  color: #333;
}

.dark-mode .dismiss-btn {
  color: #999;
}

.dark-mode .dismiss-btn:hover {
  color: #ccc;
} 