.motivational-quote {
  background: linear-gradient(135deg, rgba(255,255,255,0.1), rgba(255,255,255,0.1));
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: 1px solid rgba(255,255,255,0.2);
  border-radius: 10px;
  padding: 20px;
  margin: 20px 0;
  position: relative;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
  text-align: center;
  overflow: hidden;
  animation: fadeIn 0.5s ease-in-out;
}

.dark-mode .motivational-quote {
  background: linear-gradient(135deg, rgba(30,30,30,0.5), rgba(20,20,20,0.5));
  border: 1px solid rgba(255,255,255,0.1);
}

.quote-icon {
  font-size: 32px;
  color: rgba(122, 137, 209, 0.3);
  position: absolute;
  top: 10px;
  left: 15px;
}

.motivational-quote blockquote {
  font-size: 18px;
  font-style: italic;
  margin: 10px 0;
  padding: 0 15px;
  position: relative;
  z-index: 1;
  line-height: 1.6;
}

.motivational-quote cite {
  display: block;
  font-size: 14px;
  margin-top: 10px;
  font-weight: 500;
  opacity: 0.8;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@media (max-width: 768px) {
  .motivational-quote {
    padding: 15px;
    margin: 15px 0;
  }
  
  .motivational-quote blockquote {
    font-size: 16px;
  }
  
  .quote-icon {
    font-size: 24px;
  }
} 